import dayjs from 'dayjs'

export default {
  watch: {
    // 
    value (newVal) {
      if (dayjs(newVal).isValid()) {
        newVal = this.getValue(newVal)
      }

      this.newValue = newVal
    }
  },
  data () {
    return {
      newValue: this.value ? dayjs(this.value).format() : this.value
    }
  },
  methods: {
    //  formMixin  handleChange,  unix 
    handleChange (value) {
      let newVal = value
      if (value && !this.attrs.valueFormat) {
        newVal = dayjs(value).unix()
      }

      this.$emit('input', newVal)
    },

    // : ( -> ) / 
    getValue (value) {
      if (value && !this.attrs.valueFormat) {
        value = typeof value === 'number' ? value * 1000 : value
        return dayjs(value).format()
      } else {
        return value
      }
    },

    // 
    customInit (value) {
      return this.getValue(value)
    }
  }
}
