<template>
  <el-card
    class="demo-card"
    header="form-desc props"
    shadow="never"
  >
    <div class="form-desc-props">
      <ele-form
        :formData="formData"
        :formDesc="formDesc"
        :isResponsive="false"
        :request-fn="handleSubmit"
        @request-success="handleSuccess"
      />
      <codemirror
        @input="handleCodeChange"
        v-model="code"
      />
    </div>
  </el-card>
</template>

<script>
export default {
  inject: ['checkType'],
  data () {
    return {
      formDesc: {},
      code: `{
  name: {
    // 1.: ele-form 20  
    // https://www.yuque.com/chaojie-vjiel/vbwzgu/kz163g
    type: 'input',
    // 2.form-item  label 
    label: '',
    // 3., 
    // , input: https://element.eleme.cn/#/zh-CN/component/input#input-attributes
    attrs: {
      maxlength: 4
    },
    // 4., : 1 - 24
    layout: 12,
    // 5.
    tip: '4'
  },
  job: {
    type: 'select',
    label: '',
    layout: 12,
    // 6.
    default: 1,
    // 7.optinos: select / radio / checkbox  options 
    //   /  / Promise / 
    // https://www.yuque.com/chaojie-vjiel/vbwzgu/rgenav
    options: [
      // 7.1 
      // text: , value: , attrs: option
      { text: '', value: 1 },
      { text: '', value: 2 },
      { text: '', value: 3, attrs: { disabled: true } }
    ]
  },
  language: {
    type: 'checkbox',
    label: '',
    // 8.:  / ,   / 
    // https://www.yuque.com/chaojie-vjiel/vbwzgu/loffm6
    vif (data) { return data.job },
    // 9.options
    isReloadOptions: true,
    default: ['html'],
    // 7.2  options
    options (data) {
      let language = ['html', 'js', 'css']
      switch(data.job) {
        case 1:
          // 7.3 options: 
          // language = ['html', 'js', 'css']
          break
        case 2:
          language = language.concat(['java', 'go', 'nodejs'])
          break
        case 3:
          language = language.concat(['swift', 'Kotlin', 'flutter'])
          break
      }
      return language
    }
  },
  girlFriend: {
    type: 'radio',
    label: '',
    options: [ { text: '', value: 1 }, { text: '', value: 0 } ],
    // 10. 
    valueFormatter (value) {
      return Boolean(value)
    },
    // 11. 
    on: {
      change (val) {
        console.log(val) // 
      }
    }
  },
  girlFriendName: {
    type: 'input',
    label: '',
    vshow (data) { return data.girlFriend }
  },
  motto: {
    type: 'text',
    label: '',
    default: '',
    // 12. 
    style: {
      color: 'red'
    },
    // 13. class 
    class: ['motto-text']
  },
  activity: {
    type: 'tag',
    label: '',
    default: ',,',
    // 14.  ()
    displayFormatter (value) {
      return typeof value === 'string' ? value.split(',') : value
    }
  }
}
`,
      formData: {
        job: null,
        language: []
      }
    }
  },
  methods: {
    handleSubmit (data) {
      /* eslint-disable */
      console.log(data)
      return Promise.resolve()
    },
    handleSuccess () {
      this.$message.success('')
    },
    handleCodeChange (code) {
      try {
        /* eslint-disable */
        const codeData = eval('(' + code + ')')
        if (this.checkType(codeData)) {
          this.formDesc = codeData
        }
      } catch (error) {
        console.log(code, error)
      }
    }
  },
  created () {
    this.handleCodeChange(this.code)
  }
}
</script>


<style>
.form-desc-props .CodeMirror {
  height: 400px !important;
}
</style>
